
.slider[data-v-3a901cac] {
  --color: var(--app-main-color);

  position: relative;
  height: 24px;
  display: flex;
  align-items: center;
}
.slider-inner[data-v-3a901cac] {
  flex: 1;
  display: flex;
  align-items: center;
  padding: 0 6px;
  position: relative;
}
.slider-handle[data-v-3a901cac] {
  position: relative;
  height: 2px;
  width: 100%;
  background: var(--app-light-color);
}
.slider-track[data-v-3a901cac] {
  width: 100%;
  height: 100%;
  background: var(--color);
}
.slider-thumb[data-v-3a901cac] {
  position: absolute;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin: -7px 0 0 -6px;
  background: var(--color);
}
.slider-range[data-v-3a901cac] {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.slider-value[data-v-3a901cac] {
  font-size: 14px;
  margin-left: 4px;
  color: var(--app-grey-color);
}
