
.button[data-v-0e003a7d] {
  --font-color: var(--app-semi-color);
  --border-color: var(--app-light-color);
  --background-color: #fff;
  --icon-size: 20px;
  --icon-color: var(--app-semi-color);

  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  min-width: 64px;
  height: 32px;
  padding: 0 16px;
  font-size: 14px;
  line-height: 30px;
  border-width: 1px;
  border-style: solid;
  background: none;
}
.button.size--mini[data-v-0e003a7d] {
  min-width: 48px;
  height: 24px;
  padding: 0 8px;
  line-height: 22px;
  font-size: 12px;
  --icon-size: 16px;
}
.button.size--large[data-v-0e003a7d] {
  min-width: 72px;
  height: 40px;
  padding: 0 16px;
  line-height: 38px;
  font-size: 16px;
  --icon-size: 22px;
}
.button[data-v-0e003a7d]:not(:disabled):hover {
  opacity: 0.8;
}
.button[data-v-0e003a7d]:not(:disabled):active {
  opacity: 0.6;
}
.button[data-v-0e003a7d]:disabled {
  cursor: not-allowed;
  opacity: 0.3;
}
.button.type--primary[data-v-0e003a7d] {
  --font-color: #fff;
  --border-color: var(--app-main-color);
  --background-color: var(--app-main-color);
  --icon-color: #fff;
}
.button.type--warn[data-v-0e003a7d] {
  --font-color: #fff;
  --border-color: var(--app-warn-color);
  --background-color: var(--app-warn-color);
  --icon-color: #fff;
}
.button[data-v-0e003a7d] {
  color: var(--font-color);
  background-color: var(--background-color);
  border-color: var(--border-color);
}
.button .button-loading-icon[data-v-0e003a7d] {
  display: inline-block;
  width: 1.429em;
  height: 1.429em;
  line-height: 1;
  margin: -0.2em 0.34em 0 0;
  vertical-align: middle;
  animation: app-rotate-360 0.8s infinite linear both;
  fill: var(--icon-color);
}
