
.scroll-view[data-v-74664acf] {
  display: block;
  width: 100%;
}
.scroll-view[data-v-74664acf]::-webkit-scrollbar {
  background-color: transparent;
  width: 6px;
  height: 6px;
}
.scroll-view[data-v-74664acf]::-webkit-scrollbar-thumb {
  background-color: var(--app-grey-color);
  background-repeat: no-repeat;
