  border-radius: 3px;
}
.scroll-view.scroll-x[data-v-74664acf] {
  overflow-x: auto;
}
.scroll-view.scroll-y[data-v-74664acf] {
  overflow-y: auto;
}
.scroll-view.enable-flex[data-v-74664acf] {
