
.switch[data-v-38240d02] {
  --color: var(--app-main-color);
  --font-size: 24px;

  position: relative;
  width: 2em;
  height: 1em;
  background: var(--app-light-color);
  border: 0;
  border-radius: 1em;
  font-size: var(--font-size);
  outline: 0;
  cursor: pointer;
  transition: all 0.2s linear;
}
.switch.size--mini[data-v-38240d02] {
  --font-size: 16px;
}
.switch.size--large[data-v-38240d02] {
  --font-size: 32px;
}
.switch[checked][data-v-38240d02] {
  background-color: var(--color);
}
.switch[data-v-38240d02]:after {
  position: absolute;
  top: 1px;
  left: 1px;
  width: calc(50% - 2px);
  height: calc(100% - 2px);
  background: #fff;
  border-radius: 100%;
  transition: all 0.2s linear;
  content: '';
}
.switch[checked][data-v-38240d02]:after {
  left: calc(50% + 1px);
}
.switch[data-v-38240d02]:not(:disabled):hover {
  opacity: 0.8;
}
.switch[data-v-38240d02]:not(:disabled):active {
  opacity: 0.6;
}
.switch[data-v-38240d02]:disabled {
  cursor: not-allowed;
  opacity: 0.3;
}
