
.icon[data-v-38d2d0ef] {
  --size: 24px;
  --color: var(--app-main-color);

  display: inline-block;
  width: var(--size);
  height: var(--size);
  overflow: hidden;
  line-height: 0;
  fill: var(--color);
}
.type--success[data-v-38d2d0ef],
.type--success_no_circle[data-v-38d2d0ef],
.type--radio_checked[data-v-38d2d0ef],
.type--checkbox_checked[data-v-38d2d0ef] {
  --color: var(--app-main-color);
}
.type--info[data-v-38d2d0ef],
.type--waiting[data-v-38d2d0ef] {
  --color: var(--app-link-color);
}
.type--warn[data-v-38d2d0ef] {
