}
.type--cancel[data-v-38d2d0ef],
.type--download[data-v-38d2d0ef],
.type--upload[data-v-38d2d0ef],
.type--loading[data-v-38d2d0ef],
.type--radio[data-v-38d2d0ef],
.type--checkbox[data-v-38d2d0ef] {
