
.textarea[data-v-1e0ee5c5] {
  --line-height: 22px;
  --padding-top-bottom: 4px;
  --font-size: 14px;
  --padding-left-right: 12px;
  --color: var(--app-main-color);

  height: calc(var(--line-height) * 3 + var(--padding-top-bottom) * 2 + 2px);
  width: 100%;
  position: relative;
}
.textarea.size--mini[data-v-1e0ee5c5] {
  --line-height: 18px;
  --padding-top-bottom: 2px;
  --font-size: 12px;
  --padding-left-right: 8px;
}
.textarea.size--large[data-v-1e0ee5c5] {
  --line-height: 26px;
  --padding-top-bottom: 6px;
  --font-size: 16px;
}
.input-text[data-v-1e0ee5c5] {
  display: block;
  border: 1px solid var(--app-light-color);
  border-radius: 4px;
  overflow: hidden;
  margin: 0;
  outline: none;
  width: 100%;
  height: 100%;
  line-height: var(--line-height);
  width: 100%;
  padding: var(--padding-top-bottom) var(--padding-left-right);
  font-size: var(--font-size);
  cursor: pointer;
  color: var(--app-semi-color);
  background: #ffffff;
  box-sizing: border-box;
  resize: none;
  box-shadow: none;
}
.input-text[data-v-1e0ee5c5]::-webkit-input-placeholder {
  color: var(--app-light-color);
}
.input:hover .input-text[data-v-1e0ee5c5],
.input-text[data-v-1e0ee5c5]:hover {
  border-color: var(--color);
}
.input-text[data-v-1e0ee5c5]:disabled,
.input-text[data-v-1e0ee5c5]:disabled:hover {
  background-color: var(--app-whitesmoke-color);
  border-color: var(--app-light-color);
  cursor: not-allowed;
}
.input-text[data-v-1e0ee5c5]:focus {
  border-color: var(--color);
  box-shadow: 0 0 3px var(--color);
}
