
.cascader[data-v-73e4466e] {
  --height: 30px;
  --font-size: 14px;
  --icon-size: 20px;
  --color: var(--app-main-color);
  --dropdown-color: rgba(9, 187, 7, 0.1);
  --padding-left-right: 12px;

  display: flex;
  height: calc(var(--height) + 2px);
  font-size: var(--font-size);
  position: relative;
  color: var(--app-semi-color);
}
.cascader.size--mini[data-v-73e4466e] {
  --height: 22px;
  --font-size: 12px;
  --icon-size: 16px;
  --padding-left-right: 8px;
}
.cascader.size--large[data-v-73e4466e] {
  --height: 38px;
  --font-size: 16px;
  --icon-size: 22px;
}
.cascader-input-box[data-v-73e4466e] {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid var(--app-light-color);
  border-radius: 4px;
  overflow: hidden;
  box-sizing: border-box;
}
.cascader-input-box[data-v-73e4466e]:hover {
  border-color: var(--color);
}
.cascader.disabled .cascader-input-box[data-v-73e4466e],
.cascader.disabled .cascader-input-box[data-v-73e4466e]:hover {
  background-color: var(--app-whitesmoke-color);
  border-color: var(--app-light-color);
  cursor: not-allowed;
}
.cascader.focus .cascader-input-box[data-v-73e4466e] {
  border-color: var(--color);
  box-shadow: 0 0 3px var(--color);
}
.cascader-input[data-v-73e4466e] {
  flex: 1;
  box-sizing: border-box;
  margin: 0;
  height: 100%;
  outline: none;
  height: var(--height);
  line-height: var(--height);
  padding: 0 calc(var(--padding-left-right) / 2) 0 var(--padding-left-right);
  font-size: var(--font-size);
  border: none;
  cursor: pointer;
  user-select: none;
  color: var(--app-semi-color);
  background: none;
}
.cascader-input[data-v-73e4466e]::-webkit-input-placeholder {
  color: var(--app-light-color);
}
.cascader-input[data-v-73e4466e]:disabled {
  cursor: not-allowed;
}
.cascader-unfold-icon[data-v-73e4466e] {
  display: block;
  width: var(--icon-size);
  height: var(--icon-size);
  margin-right: var(--padding-left-right);
  transition: all 0.2s;
}
.cascader.focus .cascader-unfold-icon[data-v-73e4466e] {
  transform: rotate(180deg);
}
.cascader-dropdown[data-v-73e4466e] {
  display: none;
  position: absolute;
  left: 0;
  width: 1000%;
  top: calc(100% + 2px);
  z-index: 99999;
}
.cascader-list-group[data-v-73e4466e] {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
  border: 1px solid var(--app-light-color);
  box-sizing: border-box;
  border-radius: 4px;
  z-index: 100000;
  height: 208px;
  max-width: 600px;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}
.cascader.focus .cascader-dropdown[data-v-73e4466e] {
  display: block;
}
.cascader-list[data-v-73e4466e] {
  float: left;
  min-width: 60px;
  max-width: 200px;
  max-height: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
  user-select: none;
  border-right: 1px solid var(--app-whitesmoke-color);
  flex: 0 0 auto;
  float: none;
  display: inline-flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-x: hidden;
  overflow-y: auto;
}
.cascader-list[data-v-73e4466e]:last-child {
  border-right: none;
}
.cascader-item[data-v-73e4466e] {
  display: flex;
  align-items: center;
  height: 32px;
  line-height: 32px;
  font-size: 14px;
  padding: 0 var(--padding-left-right);
  cursor: pointer;
  user-select: none;
  width: 100%;
  box-sizing: border-box;
}
.cascader-item-text[data-v-73e4466e] {
  text-align: left;
  flex: 1;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.cascader-item-icon[data-v-73e4466e] {
  margin-right: calc(var(--padding-left-right) / 2 - var(--padding-left-right));
}
.cascader-item[selected][data-v-73e4466e] {
  background-color: var(--app-whitesmoke-color);
}
.cascader-item[data-v-73e4466e]:hover {
  background-color: var(--dropdown-color);
}
.cascader-item[disabled][data-v-73e4466e],
.cascader-item[disabled][data-v-73e4466e]:hover {
  background-color: #ffffff;
  color: var(--app-light-color);
  cursor: not-allowed;
}
@media screen and (max-width: 540px) {
.cascader-dropdown[data-v-73e4466e] {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
}
.cascader-list-group[data-v-73e4466e] {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    top: auto;
    border-radius: 0;
    border: none;
    align-items: center;
}
.cascader-item[data-v-73e4466e] {
    height: 48px;
    line-height: 48px;
    font-size: 16px;
}
}
