
.input[data-v-b0fd6d62] {
  --height: 30px;
  --font-size: 14px;
  --icon-size: 20px;
  --padding-left-right: 12px;
  --color: var(--app-main-color);
  --warn-color: var(--app-warn-color);

  height: calc(var(--height) + 2px);
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
.input.warn[data-v-b0fd6d62] {
  --color: var(--app-warn-color);
}
.input.size--mini[data-v-b0fd6d62] {
  --height: 22px;
  --font-size: 12px;
  --icon-size: 16px;
  --padding-left-right: 8px;
}
.input.size--large[data-v-b0fd6d62] {
  --height: 38px;
  --font-size: 16px;
  --icon-size: 22px;
}
.input-input[data-v-b0fd6d62] {
  display: block;
  border: 1px solid var(--app-light-color);
  border-radius: 4px;
  overflow: hidden;
  margin: 0;
  outline: none;
  width: 100%;
  height: 100%;
  line-height: var(--height);
  width: 100%;
  padding: 0 calc(var(--icon-size) + var(--padding-left-right) * 1.5) 0
    var(--padding-left-right);
  font-size: var(--font-size);
  cursor: pointer;
  color: var(--app-semi-color);
  background: #ffffff;
  box-sizing: border-box;
  box-shadow: none;
}
.input-input[type='search'][data-v-b0fd6d62]::-webkit-search-cancel-button {
  display: none;
}
.input.has-icon .input-input[data-v-b0fd6d62] {
  padding-left: calc(var(--icon-size) + var(--padding-left-right) * 1.5);
}
.input.warn .input-input[data-v-b0fd6d62] {
  border-color: var(--warn-color);
}
.input-input[data-v-b0fd6d62]::-webkit-input-placeholder {
  color: var(--app-light-color);
}
.input:hover .input-input[data-v-b0fd6d62],
.input-input[data-v-b0fd6d62]:hover {
  border-color: var(--color);
}
.input-input[data-v-b0fd6d62]:disabled,
.input-input[data-v-b0fd6d62]:disabled:hover {
  background-color: var(--app-whitesmoke-color);
  border-color: var(--app-light-color);
  cursor: not-allowed;
}
.input-input[data-v-b0fd6d62]:focus {
  border-color: var(--color);
  box-shadow: 0 0 3px var(--color);
}
.input-label-icon[data-v-b0fd6d62],
.input-clear-icon[data-v-b0fd6d62] {
  position: absolute;
  right: var(--padding-left-right);
  /* right: calc((var(--height) - var(--icon-size)) / 2); */
  top: 50%;
  margin-top: calc((var(--icon-size) / 2) - var(--icon-size));
  width: var(--icon-size);
  height: var(--icon-size);
  box-sizing: border-box;
  cursor: pointer;
}
.input-label-icon[data-v-b0fd6d62] {
  left: var(--padding-left-right);
  right: auto;
}
.input-error[data-v-b0fd6d62] {
  position: absolute;
  left: var(--padding-left-right);
  top: 100%;
  font-size: 12px;
  height: 14px;
  line-height: 14px;
  padding: 0 5px;
  margin-top: -8px;
  background-color: #fff;
  color: var(--warn-color);
}
