
.select[data-v-d4255972] {
  --height: 30px;
  --font-size: 14px;
  --icon-size: 20px;
  --padding-left-right: 12px;
  --color: var(--app-main-color);

  display: inline-flex;
  width: 100%;
  height: calc(var(--height) + 2px);
  font-size: var(--font-size);
  position: relative;
}
.select.size--mini[data-v-d4255972] {
  --height: 22px;
  --font-size: 12px;
  --icon-size: 16px;
  --padding-left-right: 8px;
}
.select.size--large[data-v-d4255972] {
  --height: 38px;
  --font-size: 16px;
  --icon-size: 22px;
}
.select-input-box[data-v-d4255972] {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid var(--app-light-color);
  border-radius: 4px;
  overflow: hidden;
  box-sizing: border-box;
}
.select-input-box[data-v-d4255972]:hover {
  border-color: var(--color);
}
.select.disabled .select-input-box[data-v-d4255972],
.select.disabled .select-input-box[data-v-d4255972]:hover {
  background-color: var(--app-whitesmoke-color);
  border-color: var(--app-light-color);
  cursor: not-allowed;
}
.select.focus .select-input-box[data-v-d4255972] {
  border-color: var(--color);
  box-shadow: 0 0 3px var(--color);
}
.select-input[data-v-d4255972] {
  flex: 1;
  box-sizing: border-box;
  margin: 0;
  height: 100%;
  outline: none;
  height: var(--height);
  line-height: var(--height);
  padding: 0 calc(var(--padding-left-right) / 2) 0 var(--padding-left-right);
  font-size: var(--font-size);
  border: none;
  cursor: pointer;
  user-select: none;
  color: var(--app-semi-color);
  background: none;
}
.select-input[data-v-d4255972]::-webkit-input-placeholder {
  color: var(--app-light-color);
}
.select-input[data-v-d4255972]:disabled {
  cursor: not-allowed;
}
.select-unfold-icon[data-v-d4255972] {
  display: block;
  width: var(--icon-size);
  height: var(--icon-size);
  margin-right: var(--padding-left-right);
  transition: all 0.2s;
}
.select.focus .select-unfold-icon[data-v-d4255972] {
  transform: rotate(180deg);
}
.select-dropdown[data-v-d4255972] {
  display: none;
  position: absolute;
  left: 0;
  top: calc(100% + 2px);
  background-color: #fff;
  min-width: 100%;
  max-width: calc(100% + 100px);
  border: 1px solid var(--app-light-color);
  box-sizing: border-box;
  border-radius: 4px;
  z-index: 99999;
}
.select.focus .select-dropdown[data-v-d4255972] {
  display: block;
}
